import { Component, OnInit } from '@angular/core';
import { PostService } from '../post.service';

@Component({
  selector: 'app-tech-crunch',
  templateUrl: './tech-crunch.component.html',
  styleUrls: ['./tech-crunch.component.scss']
})
export class TechCrunchComponent implements OnInit {

  news;
  status;
  newsData;

  constructor(private post_service: PostService) { }

  ngOnInit() {
    this.News();
  }

  News() {
    this.post_service.GetTechCrunch()
      .subscribe(
        (response: Response) => {
          this.news = response;
          this.newsData = this.news.articles;
          console.log(response);
        },
        (error) => {
          console.log(error)
          console.log(error.status)
          this.status = error.status;
        }
      );
  }


}
