import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BitcoinComponent } from './bitcoin/bitcoin.component';
import { BusinesHeadlinesComponent } from './busines-headlines/busines-headlines.component';
import { AppleComponent } from './apple/apple.component';
import { TechCrunchComponent } from './tech-crunch/tech-crunch.component';
import { WallStreetComponent } from './wall-street/wall-street.component';
import { GoogleNewsComponent } from './google-news/google-news.component';
import { GoogleItalyComponent } from './google-italy/google-italy.component';
import { GoogleSaudiComponent } from './google-saudi/google-saudi.component';
import { HomeComponent } from './home/home.component';

const routes: Routes = [
  { path: 'bitcoin', component: BitcoinComponent },
  { path: 'business_headlines', component: BusinesHeadlinesComponent },
  { path: 'apple', component: AppleComponent },
  { path: 'techcrunch', component: TechCrunchComponent },
  { path: 'wall_street', component: WallStreetComponent },
  { path: 'google', component: GoogleNewsComponent },
  { path: 'italy', component: GoogleItalyComponent },
  { path: 'saudi', component: GoogleSaudiComponent },
  { path: '', component: HomeComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
