import { Component, OnInit } from '@angular/core';
import { PostService } from '../post.service';

@Component({
  selector: 'app-busines-headlines',
  templateUrl: './busines-headlines.component.html',
  styleUrls: ['./busines-headlines.component.scss']
})
export class BusinesHeadlinesComponent implements OnInit {

  news;
  newsData;

  constructor(private post_service: PostService) { }

  ngOnInit() {
    this.News();
  }

  News() {
    this.post_service.GetBusinessHeadlines()
      .subscribe(
        (response: Response) => {
          this.news = response;
          this.newsData = this.news.articles;
          console.log(response);
        },
        (error) => console.log(error)
      );
  }


}
