import { Component, OnInit } from '@angular/core';
import { PostService } from '../post.service';

@Component({
  selector: 'app-google-italy',
  templateUrl: './google-italy.component.html',
  styleUrls: ['./google-italy.component.scss']
})
export class GoogleItalyComponent implements OnInit {

  news;
  newsData;

  constructor(private post_service: PostService) { }

  ngOnInit() {
    this.News();
  }

  News() {
    this.post_service.GetGoogleItaly()
      .subscribe(
        (response: Response) => {
          this.news = response;
          this.newsData = this.news.articles;
          console.log(response);
        },
        (error) => console.log(error)
      );
  }
}
